import { AspectRatio, Box, Stack, Table, Typography } from "@mui/joy";
import React from "react";
import { Auth } from "../../Shares/Auth";
import { Events } from "../../util/EventEmitter";

export default function Gallery() {

	const [images, setImages] = React.useState<any[]>([]);

	React.useEffect(() => {
		Auth.shared.subscribe(Events.GALLERY_FETCHED, (gallery) => {
			setImages(gallery);
		});

		Auth.shared.fetchGallery();
	}, []);

	return (
		<Stack direction={"column"} justifyContent={"center"} alignItems={"center"}>
			<Box sx={{width: "60%"}}>
				<Typography level="h1">Gallery</Typography>

				<Table>
					<thead>
						<tr>
							<th>Id</th>
							<th>Upload Token</th>
							<th></th>
						</tr>
					</thead>
					<tbody>
						{images.map((image, index) => {
							return (
								<tr key={index}>
									<td>
										<AspectRatio ratio={16/9}>
											<img src={image.url} style={{width: "100%", height: "100%", objectFit: "cover"}} />
										</AspectRatio>
									</td>
									<td>{image.uploader}</td>
									<td>{image.uploadDate}</td>
								</tr>
							);
						})}
					</tbody>
				</Table>
			</Box>
		</Stack>
	);
}